import { FormControl, NgForm, Validators } from '@angular/forms';
import { ApplicationService } from './../applications.service';
import { Component, OnInit } from '@angular/core';
import { NgModule } from '@angular/core';
import { Router } from '@angular/router';
import { LeaveType } from '../LeaveType.model';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-leave',
  templateUrl: './leave.component.html',
  styleUrls: ['./leave.component.scss']
})
export class LeaveComponent implements OnInit {
  // LeaveTypeControl = new FormControl('', [Validators.required]);
  CommenceDateControl = new FormControl('', [Validators.required]);
  EndDateControl = new FormControl('', [Validators.required]);
  ResumeDateControl = new FormControl('', [Validators.required]);

  staffname: any;
  leavetype: LeaveType[];
  department: any;
  minDate = new Date();
  SelectedLeaveType: '';
  StartDate: Date;
  EndDate: Date;
  ResumeDate: Date;
  oneday: any;

  differencems: any;
  totaldays: any;


  constructor(private applicationservice: ApplicationService,
              private router: Router) { }

  ngOnInit() {
    this.staffname = localStorage.getItem('profilename');
    this.department = localStorage.getItem('department');
    this.applicationservice.GetLeaveType().subscribe
    (result => { this.leavetype = result;
    }, error => console.error(error));
    }

  GetDaysBetween(date1: Date, date2: Date) {
  this.differencems = Math.floor((Date.UTC(date2.getFullYear(), date2.getMonth(),
  date2.getDate()) - Date.UTC(date1.getFullYear(), date1.getMonth(),
  date1.getDate()) ) / (1000 * 60 * 60 * 24));
  console.log(this.differencems);
  }

  ApplyLeave (leaveForm: NgForm) {
    console.log(this.staffname);
    console.log(this.department);
    console.log(leaveForm.value.leaveType);
    console.log(leaveForm.value.comdate);
    console.log(leaveForm.value.endate);
    console.log(leaveForm.value.resdate);
    console.log(this.differencems);
  }
}
