import { LeaveComponent } from './leave/leave.component';

import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MydetailsComponent } from './mydetails/mydetails.component';

const ApplicationsRoutes: Routes = [
    { path: 'leave', component:  LeaveComponent , data: { animation: 'buttons' }},
    { path: 'chatlogin', component: MydetailsComponent, data: {animation: 'buttons'}},
    { path: '', redirectTo: '/auth/dashboard', pathMatch: 'full'}
];

@NgModule({
    imports: [
        RouterModule.forChild(ApplicationsRoutes)
    ],
    exports: [
        RouterModule
    ]
})

export class ApplicationsRouterModule {}
